import { NavLink } from "react-router-dom";
import styled from "styled-components";

const CategoryWrapper = styled.div`
  margin-bottom: 20px;
  font-size: 1.5rem;
  .cat {
    margin-right: 5px;
  }
`;

const categoryList = [
  {
    name: "all",
    text: " ",
  },
  {
    name: "business",
    text: "",
  },
  {
    name: "entertainment",
    text: "",
  },
  {
    name: "health",
    text: "",
  },
  {
    name: "science",
    text: "",
  },
  {
    name: "sports",
    text: "",
  },
  {
    name: "technology",
    text: "",
  },
];

const Categories = () => {
  return (
    <CategoryWrapper>
      {categoryList.map((cat) => {
        return (
          <NavLink
            key={cat.name}
            to={cat.name === "all" ? "/news" : `/news/${cat.name}`}
            className="cat"
          >
            {cat.name}
          </NavLink>
        );
      })}
    </CategoryWrapper>
  );
};

export default Categories;
